import React, { Component } from 'react';
import { translate } from 'react-i18next';
import DateRange from '@material-ui/icons/DateRange';
import Animated from './Animated';
import { getAllMonths } from '../utils';

export class Experience extends Component {
  componentDidMount() {
    this.props.getExpRequest();
  }
  render() {
    const { data, t } = this.props;

    return (
      data && (
        <Animated>
          {data.map(
            (item, key) =>
              item && (
                <div key={key} className="block">
                  <p>
                    <b>{t('Position')}: </b>
                    {item.position} | <b>{t('Company')}: </b>
                    {item.company}
                  </p>
                  <p className="date">
                    <span>
                      <DateRange />{' '}
                      <span>
                        {t(item.from)} - {t(item.to)}.{' '}
                        {getAllMonths(
                          item.from,
                          item.to,
                          t('year(s)'),
                          t('month(s)')
                        )}
                      </span>
                    </span>
                  </p>
                  <p>{t(item.responsibility)}</p>
                </div>
              )
          )}
        </Animated>
      )
    );
  }
}

export default translate('translations')(Experience);



